import $T from '../../common/lib/time.js';
export default {
	data() {
		return {
			page:1,//页数
			dataIndex:"",
			cateIndex:0,
			catelist:[
				{"name":"全部",value:""},
				{"name":"好评",value:"/good"},
				{"name":"中评",value:"/middle"},
				{"name":"差评",value:"/bad"}
			],
			list:[],
			total:0, //总品论 数
			good_rate:0,//好评率 
			loadtext:"上拉加载"
		}
	},
	// 上拉加载生命周期
	onReachBottom() {
		this.page++
		this.getData()
	},
	
	
	methods: {
		change(index){
			this.cateIndex = index
			uni.showLoading({
				title: '加载中',
				mask: false
			});
			this.getData((res)=>{
				uni.hideLoading()
				if(res){
					uni.showToast({
						title: '加载成功',
						icon:"none"
					});
				}
			})
		},
		// 初始化
		__init(){
			this.$.get(`/goods/${this.dataIndex}/comments?page=${this.page}`).then(res=>{
				console.log(res) 
				this.total = res.total,
				this.good_rate = res.good_rate,
				this.list = res.list
			})
		},
		// 加载数据
		getData(callback=false,refresh = false){
			let value = this.catelist[this.cateIndex].value
			console.log(value)
			this.$.get(`/goods/${this.dataIndex}/comments${value}?page=${this.page}`).then(res=>{
				this.total = res.total,
				this.good_rate = res.good_rate,
				console.log(callback,refresh)
				this.list = !refresh? [...this.list,...res.list] :[...res.list]
			})
			if(typeof callback ==="function"){
				callback(true)
			}
		}
	},
	// 监听下拉刷新
	onPullDownRefresh(){
		console.log("下拉刷新")
		uni.stopPullDownRefresh()
	},
	// 筛选数据
	filters:{
		formatime(value){
			return $T.gettime(value);
		},
		rating(value){
			if(value===3){
				return "中评"
			}else if(value>3){
				return "好评"
			}else{
				return "差评"
			}
		}
	},
	// 页面渲染获取页面传染的 id
	onLoad(e) {
		this.dataIndex= e.id
		this.__init()
		console.log(e.id)
	}
}
